


































































import { Component, Vue, Prop } from "vue-property-decorator";

export interface IHeaderType {
  prevBtn?: string;
  cookieBtn?: string;
  textBtn?: string[];
  extraBtn?: string[];
  zzBtn?: string;
  searchBtn?: string;
}

@Component({
  components: {},
})
export default class GlobalHeader extends Vue {
  @Prop() private title!: string;
  @Prop() private type!: IHeaderType;
  private sc = true;
  private titleOpacity = 1;
  private textBtnOpacity = 1;
  private aF: any = null;

  private get isRoute() {
    return this.$route.path.split("/")[1];
  }

  private scrollEvent() {
    if (this.$global.$el.scrollTop === 0) {
      cancelAnimationFrame(this.aF);
      this.textBtnOpacity = 1;
      this.titleOpacity = 0;
      this.$el.classList.remove("under-border");
      return;
    }

    if (this.$global.$el.scrollTop >= 100) {
      this.titleOpacity = 1;
      this.textBtnOpacity = 0;
      return;
    }

    if (this.sc) {
      this.sc = false;
      this.aF = requestAnimationFrame(this.titleEvent);
    }
  }

  private titleEvent() {
    this.textBtnOpacity = 0;
    this.$el.classList.remove("under-border");
    this.$el.classList.add("under-border");
    this.titleOpacity = this.$global.$el.scrollTop / 100;
    this.sc = true;
  }

  protected mounted() {
    if (this.isRoute === "recommend") {
      this.$global.$el.addEventListener("scroll", this.scrollEvent);
      this.scrollEvent();
    }
  }

  protected beforeDestroy() {
    this.$global.$el.removeEventListener("scroll", this.scrollEvent);
  }
}
